@charset "UTF-8";
h1 {
  font-size: 100px;
  font-weight: 100;
  text-align: center;
  color: rgba(45, 20, 200, 0.25);
  text-rendering: optimizeLegibility; }

:focus {
  outline: 0; }

::placeholder {
  font-style: italic;
