          padding: 16px 127px 16px 30px; }
          .notepad form .text-input-wrapper input[type="text"] {
            font-size: 20px;
            height: 30px; } }
      .notepad form .text-input-wrapper input {
        height: 34px;
        width: 100%;
        font-size: 24px;
        font-family: inherit;
        font-weight: inherit;
        line-height: 1.4em;
        border: 0;
