  .notepad .list {
    position: relative;
    border-top: 1px solid #e6e6e6; }
    .notepad .list:before {
      content: '';
      position: absolute;
      right: 0;
      bottom: 0;
      left: 0;
      height: 50px;
      overflow: hidden;
      box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2), 0 8px 0 -3px #f6f6f6, 0 9px 1px -3px rgba(0, 0, 0, 0.2), 0 16px 0 -6px #f6f6f6, 0 17px 2px -6px rgba(0, 0, 0, 0.2); }
    .notepad .list ul {
      margin: 0;
      padding: 0;
      list-style: none; }
      .notepad .list ul li {
        position: relative;
        font-size: 24px;
        border-bottom: 1px solid #ededed;
        word-break: break-all;
        padding: 15px 15px 15px 60px;
        line-height: 1.2;
        transition: color 0.4s; }
        @media only screen and (max-width: 600px) {
          .notepad .list ul li {
            padding: 15px 15px 15px 30px; } }
        .notepad .list ul li .remove {
          display: block;
          position: absolute;
          top: 0;
          right: 10px;
          bottom: 0;
          width: 40px;
          height: 40px;
          margin: auto 0 auto 11px;
          font-size: 30px;
          color: #cc9a9a;
          transition: color 0.2s ease-out;
          cursor: pointer;
          padding: 0;
          border: 0;
          background: none;
          vertical-align: baseline;
          font-family: inherit;
          font-weight: inherit;
          -webkit-appearance: none;
          appearance: none;
          -webkit-font-smoothing: antialiased;
          -moz-osx-font-smoothing: grayscale; }
          .notepad .list ul li .remove:hover {
            color: #af5b5e; }
          .notepad .list ul li .remove:after {
            content: 'Ã—'; }
          @media (hover: hover) and (pointer: fine) {
            .notepad .list ul li .remove {
              display: none; } }
        .notepad .list ul li:hover .remove {
          display: block; }
        .notepad .list ul li .todo-text {
          padding: 0 110px 0 0; }
          @media only screen and (max-width: 600px) {
            .notepad .list ul li .todo-text {
              font-size: 20px; } }
        .notepad .list ul li .todo-meta {
